import React from 'react';
import PropTypes from 'prop-types';

const Evento = (props) => {

    const {name} = props.info;
    if(!name) return null;
    let desc = props.info.description.text;
    if(desc !== null && desc.length > 250){
        desc = desc.substr(0,250);
    }

    return ( 
        <div>
            <div className="uk-card uk-card-default">
            <div className="uk-card-media-top">
            {props.info.logo !== null ?
                <img src={props.info.logo.url} alt={props.info.name.text}/>
                :''
            }
                </div>
            <div className="uk-card-body">
                <h3 className="uk-card-title">{props.info.name.text}</h3>
                
                {desc !== null ?
                <p>{desc}</p>
                :''}
                {props.info.show_pick_a_seat === true ?
                <p className="uk-alert uk-alert-success"><i className="fas fa-chair"></i>Hay plazas libres</p>
                :<p className="uk-alert uk-alert-danger"><i className="far fa-times-circle"></i>No hay plazas libres</p>
                }
                {props.info.is_free === true ?
                <p className="uk-alert uk-alert-success"><i className="fas fa-check"></i>Evento Gratuito</p>
                :<p className="uk-alert uk-alert-danger"><i className="fas fa-euro-sign"></i>Evento de pago</p>

                }
                {props.info.created !== null ?
                <p>Creado: {props.info.created}</p>
                :''}
            </div>
            <div className="uk-card-footer">
            <a className="uk-button uk-button-secondary" href={props.info.url} target="_blank" rel="noopener noreferrer">
            Más información</a>
            
            </div>
            

            </div>
        </div>
     )
}
Evento.propTypes = {
    info : PropTypes.object.isRequired
}

export default Evento;