import React, {Component} from 'react';
import Header from './componentes/Header';
import Formulario from './componentes/Formulario';
import Eventos from './componentes/Eventos';
class App extends Component {
  token = 'WAVPALUONNEN2WV56WF7';
  ordenar = 'date';
  state = {
    categorias : [],
    eventos : []
  }
  componentDidMount() {
    this.obtenerCategorias();
    this.obtenerEventosInicio();
  
  }

  obtenerCategorias = async () => {
    let url = `https://www.eventbriteapi.com/v3/categories/?token=${this.token}&locale=es_ES`;
    await fetch(url)
    .then (respuesta => {
      return respuesta.json();
    })
    .then(categorias => {
      this.setState({
        categorias : categorias.categories
      }) 
    })
    .catch(error => {
      console.log(error);
    })
  }
  obtenerEventos = async (busqueda) => {
    let url = `https://www.eventbriteapi.com/v3/events/search/?q=${busqueda.nombre}&categories=${busqueda.categoria}&sort_by=${this.ordenar}&token=${this.token}&locale=es_ES`;
    await fetch(url)
    .then (respuesta => {
      return respuesta.json();
    })
    .then(eventos => {
      this.setState({
        eventos : eventos.events
      }) 
    })
    .catch(error => {
      console.log(error);
    })
  }
  obtenerEventosInicio = async () => {
    let url = `https://www.eventbriteapi.com/v3/events/search/?q=javascript&categories=102&sort_by=${this.ordenar}&token=${this.token}&locale=es_ES`;
    await fetch(url)
    .then (respuesta => {
      return respuesta.json();
    })
    .then(eventos => {
      this.setState({
        eventos : eventos.events
      }) 
    })
    .catch(error => {
      console.log(error);
    })
  }
  render() { 
    return ( 
      <div className="App">
      <Header/>
      <div className="uk-container">
      <Formulario
      categorias={this.state.categorias}
      obtenerEventos={this.obtenerEventos}
      />
      <Eventos 
      eventos={this.state.eventos}
      />
      </div>
    </div>
     )
  }
}
 
export default App;
